@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Poppins", sans-serif;
}

.color-light {
  color: white;
}

.color-dark {
  color: #301566;
}

h1 {
  font-family: "Poppins", sans-serif;
  font-size: 80px;
  font-weight: 600;
  line-height: 40px;
  margin-bottom: 20px;
}

h2 {
  font-family: "Poppins", sans-serif;
  font-size: 45px;
  font-weight: 800;
  line-height: 54px;
  margin-bottom: 30px;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 25px;
  font-weight: 800;
  line-height: 40px;
  margin-bottom: 30px;
}

h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-weight: 800;
  line-height: 24px;
}

p {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 50px;
}

hr {
  border-top: 2px solid #301566;
  margin: 10px 0 30px 0;
}

a.button {
  padding: 20px 40px;
  background-color: #00A015;
  margin: 0 auto;
  border-radius: 50vh;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 15px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  padding: 20px 20px;
}
header .container {
  max-width: 1170px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
header .container .logo {
  display: flex;
}
header .container nav {
  display: flex;
  align-items: center;
}
header .container nav ul {
  display: flex;
}
header .container nav ul li {
  margin: 0 10px;
  list-style: none;
}
header .container nav ul li a {
  font-size: 16px;
  font-weight: 500;
  color: #301566;
}

.hero {
  position: relative;
}
.hero .background-image {
  background: url("../img/1.jpg") no-repeat 58% 75%;
  background-size: cover;
  position: absolute;
  height: 100%;
  width: 100%;
  filter: brightness(80%) grayscale(20%);
  z-index: -1;
}
.hero .container {
  padding: 110px 40px 90px 40px;
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
}

.text {
  padding: 100px 20px 0 20px;
}
.text .container {
  display: flex;
  flex-direction: column;
  text-align: center;
  max-width: 725px;
  margin: 0 auto;
  padding: 20px 20px 0 20px;
}
.text .container h2 {
  margin-bottom: 20px;
}
.text .container p {
  margin-bottom: 30px;
}

.gallery {
  display: flex;
  gap: 7px;
}
.gallery img {
  width: 33.33333%;
}

.blocks {
  background-color: #EEEEEE;
  padding: 40px 20px 50px 20px;
}
.blocks .container {
  max-width: 1050px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.blocks .container .block {
  padding: 40px 20px;
  background-color: white;
  width: 33.33333%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.blocks .container .block .icon {
  height: 88px;
  width: 88px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}
.blocks .container .block .icon img {
  height: 55px;
}
.blocks .container .block h4 {
  text-align: center;
  margin-bottom: 20px;
}
.blocks .container .block p {
  text-align: center;
  margin-bottom: 30px;
}

.img-right {
  padding: 60px 20px;
}
.img-right .container {
  display: flex;
  margin: 20px;
  max-width: 1170px;
  margin: 20px auto;
}
.img-right .container .content {
  width: 50%;
  padding: 20px 60px 20px 40px;
}
.img-right .container .content h2 {
  margin-bottom: 20px;
}
.img-right .container .content p {
  text-align: left;
  margin-bottom: 30px;
}
.img-right .container .content .buttons {
  margin-top: 40px;
  display: flex;
  justify-content: flex-start;
  gap: 20px;
}
.img-right .container .content .buttons a {
  margin: 0;
}
.img-right .container .image {
  height: 620px;
  width: 50%;
}
.img-right .container .image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.counters {
  padding: 80px 20px 40px 20px;
  background-color: #00A015;
}
.counters .container {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
}
.counters .container .counter {
  text-align: center;
  color: white;
  width: 33.33333%;
}
.counters .container .counter h4 {
  margin-bottom: 20px;
}

.img-left {
  padding: 60px 20px;
}
.img-left .container {
  display: flex;
  margin: 20px;
  max-width: 1100px;
  margin: 20px auto;
}
.img-left .container .content {
  width: 50%;
  padding: 20px 40px;
}
.img-left .container .content h2 {
  margin-bottom: 20px;
}
.img-left .container .content p {
  text-align: left;
  margin-bottom: 30px;
}
.img-left .container .content .buttons {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
}
.img-left .container .content .buttons a {
  margin: 0;
}
.img-left .container .image {
  height: 620px;
  width: 50%;
}
.img-left .container .image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.map {
  background-color: #EEEEEE;
  padding: 80px 40px;
}
.map .container {
  display: flex;
  margin: 20px;
  max-width: 1100px;
  margin: 20px auto;
}
.map .container .content {
  background-color: white;
  width: 50%;
  padding: 20px 40px;
}
.map .container .content h3 {
  margin-bottom: 20px;
}
.map .container .content p {
  text-align: left;
  margin-bottom: 30px;
}
.map .container .content hr {
  border-width: 4px;
  border-color: #EEEEEE;
}
.map .container .content hr.top {
  margin: 10px 0 20px 0;
}
.map .container .content hr.bottom {
  margin: 20px 0;
}
.map .container .content .buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}
.map .container .content .buttons a {
  margin: 0;
  padding: 0;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.map .container .content .buttons a img {
  height: 20px;
}
.map .container .content .address {
  display: flex;
  justify-content: center;
}
.map .container .content .address a {
  display: flex;
  align-items: center;
  gap: 15px;
  padding-top: 5px;
}
.map .container .content .address a img {
  height: 16px;
}
.map .container .content .address a p {
  font-weight: 600;
  margin: 0;
}
.map .container .image {
  width: 50%;
}
.map .container .image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.contact {
  background-color: #C8C8C8;
  padding: 60px 20px;
}
.contact .container {
  display: flex;
  margin: 20px;
  max-width: 1100px;
  margin: 20px auto;
}
.contact .container .content {
  width: 50%;
  padding: 20px 40px;
}
.contact .container .content h2 {
  margin-bottom: 20px;
}
.contact .container .content p {
  text-align: left;
  margin-bottom: 30px;
  font-size: 21px;
}
.contact .container .buttons {
  width: 50%;
  padding: 20px 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.contact .container .buttons a.button {
  font-size: 20px;
  padding: 25px 135px;
  margin: 0;
}
.contact .container .image {
  height: 620px;
  width: 50%;
}
.contact .container .image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.footer-top {
  padding: 40px 20px 50px 20px;
}
.footer-top .container {
  max-width: 1170px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.footer-top .container .block {
  padding: 40px 20px;
  width: 33.33333%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.footer-top .container .block h4 {
  text-align: center;
  margin-bottom: 20px;
}
.footer-top .container .block p {
  text-align: left;
  margin-bottom: 0;
}
.footer-top .container .block a {
  font-weight: 700;
}
.footer-top .container .block:first-child a {
  font-weight: 400;
  color: #301566;
}
.footer-top .container .block:nth-of-type(2) p {
  margin-bottom: 40px;
}
.footer-top .container .block:nth-of-type(2) img {
  width: 150px;
}
.footer-top .container .block:last-child p:last-of-type {
  margin-bottom: 20px;
}

.footer-bottom {
  background-color: #C8C8C8;
  padding: 15px 0;
}
.footer-bottom .container {
  max-width: 1170px;
  display: flex;
  justify-content: flex-start;
  margin: 0 auto;
  font-weight: 500;
  color: white;
}